.header {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    border-radius: 0px 0px 16px 16px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
}

.header-contents {
    width: 90%;
    height: 80%;
    margin: auto;
    padding: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    //background-color: #33335533;
    border-radius: 16px;
}
