.image-card {
    height: 100%;
    flex: 1 0.5 40%;
    box-sizing: border-box;
    padding: 0%;
    margin: 5%;
    border-radius: 16px;
    box-shadow: 0px 2px 2px #18182533;
    transition: box-shadow 0.5s;
    overflow: hidden;
}

@media screen and (max-width: 600px) {
    .image-card {
        flex: 1 0.5 80%;
    }
}

.image-card:hover {
    box-shadow: 0px 6px 6px #18182533;
}

img {
    display:block;
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
    object-fit: cover;
}
