.nav-bar {
    width: 100%;
    height: 9vh;
    margin: 0px;
    padding: auto;
    display: flex;
    position: fixed;
    top: 0px;
    overflow: hidden;
    border-radius: 0px 0px 16px 16px;
    box-shadow: 0px 8px 8px #18182533;
}

.nav-contents {
    width: 90%;
    height: 80%;
    margin: auto;
    display: flex;
    align-items: center;
}
