.card-content {
    height: 100%;
    flex: 1 0.5 40%;
    box-sizing: border-box;
    padding: 5%;
    margin: 5%;
    border-radius: 16px;
    box-shadow: 0px 2px 2px #18182533;
    transition: box-shadow 0.5s;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

@media screen and (max-width: 600px) {
    .card-content {
        flex: 1 0.5 80%;
    }
}

.card-content:hover {
    box-shadow: 0px 6px 6px #18182533;
}
