.fill {
    height: 100vh;
    overflow: auto;
}

.black {
    background-color: #454565;
    color: #dfdfef;
}

.white {
    color: #454565;
    background-color: #dfdfef;
}

.dark-blue {
    background-color: #181825;
    color: #dfdfef;
}

p {
    font-size: 16px;
}

hr.white {
    border-style: solid;
    border-color: #dfdfef;
}

hr.black {
    border-style: solid;
    border-color: #454565;
}

.top-logo
{
    display: block;
    padding: 0px;
    margin: 16px auto;
    width: 20%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

.nav-logo
{
    display: block;
    margin: 0px;
    float: left;
    width: auto;
    height: 70%;
    max-height: 70%;
}
